"use server";

import { summarizeVideo, SummarizeVideoInput } from "@/ai/flows/video-summary";
import * as z from "zod";

const formSchema = z.object({
  videoUrl: z.string().url(),
});

type Result =
  | { success: true; data: { summary: string } }
  | { success: false; error: string };

export async function summarizeVideoAction(
  values: z.infer<typeof formSchema>
): Promise<Result> {
  const validatedFields = formSchema.safeParse(values);

  if (!validatedFields.success) {
    return {
      success: false,
      error: "Invalid input.",
    };
  }

  try {
    const input: SummarizeVideoInput = {
      videoUrl: validatedFields.data.videoUrl,
    };
    const output = await summarizeVideo(input);
    return { success: true, data: output };
  } catch (e) {
    console.error(e);
    if (e instanceof Error && e.message.includes('media')) {
       return {
        success: false,
        error: "Could not process the video from this URL. Please try a different one. (Note: This is a demo and may not support all video URLs).",
      };
    }
    return {
      success: false,
      error: "An unexpected error occurred. Please try again later.",
    };
  }
}
